import { DataTable } from 'simple-datatables';

function toggleClassEl(el, className) {
  el.classList.toggle(className);
}
function checkBoxAll(checkboxs, activeRows) {
  const checkAll = document.querySelector('#checkall');
  checkAll.addEventListener('change', (e) => {
    if (e.target.checked) {
      checkboxs.forEach((checkbox) => {
        const c = checkbox;
        c.checked = true;
        const indexRow = checkbox.getAttribute('data-row');
        activeRows[indexRow].classList.add('select');
      });
    } else {
      checkboxs.forEach((checkbox) => {
        const c = checkbox;
        c.checked = false;
        const indexRow = checkbox.getAttribute('data-row');
        activeRows[indexRow].classList.remove('select');
      });
    }
  });
}

function updateCheckAll(checkboxs, checkAll) {
  const currentCheckAll = checkAll;
  const result = checkboxs.filter((c) => c.checked === false);
  if (result.length === 0) {
    currentCheckAll.indeterminate = false;
    currentCheckAll.checked = true;
  }
}
function updateIndeterminate(checkboxs, checkAll) {
  const currentCheckAll = checkAll;
  const result = checkboxs.filter((c) => c.checked === true);
  if (result.length === 0) {
    currentCheckAll.indeterminate = false;
  }
}

function checkBoxChange(checkboxs, activeRows) {
  const checkAll = document.querySelector('#checkall');
  checkboxs.forEach((checkbox) => {
    checkbox.addEventListener('change', (e) => {
      checkAll.indeterminate = true;
      const indexRow = checkbox.getAttribute('data-row');
      toggleClassEl(activeRows[indexRow], 'select');
      if (!e.target.checked && checkAll.checked) {
        checkAll.checked = false;
      } else if (!e.target.checked && checkAll.indeterminate) {
        updateIndeterminate(checkboxs, checkAll);
      } else {
        updateCheckAll(checkboxs, checkAll);
      }
    });
  });
}

function createDataTable(tableEl) {
  const dataTable = new DataTable(tableEl, {
    fixedColumns: false,
    perPageSelect: [5, 10, 50, 100],
    columns: [
      {
        select: 0,
        sortable: false,
        render: (data, cell, row) =>
          `<div class="ctn-checkbox"><input type="checkbox" class="checkbox" data-row=${row.dataIndex}></div>`,
      },
    ],
  });
  return dataTable;
}

function run(table) {
  const tableEl = document.querySelector(table);
  const rows = [...tableEl.tBodies[0].rows];
  rows.forEach((row) => {
    row.insertCell(0);
  });
  const datatable = createDataTable(table);
  const activeRows = [...datatable.activeRows];
  const checkbox = [];

  datatable.on('datatable.init', () => {
    activeRows.forEach((row) => {
      row.cells[0].classList.add('row-checkbox');
      checkbox.push(row.cells[0].firstElementChild.firstElementChild);
    });
    checkBoxAll(checkbox, activeRows);
    checkBoxChange(checkbox, activeRows);
  });
}
run('.table');
